@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Lato', sans-serif;
}

a:hover {
  background-color: yellow;
  color: black;
  text-decoration: none;
}

body {
  color: black;
  background-color: #FFFFFF;
  font-size: 3vmin;
  line-height: 1em;
}

p a {
  color:black;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  -webkit-text-decoration-style: dotted;
          text-decoration-style: dotted;
  text-decoration-thickness: 1px;
  text-underline-offset: .3em;
}

h1 {
  font-family:  'Bebas Neue', sans-serif;
  font-size:  10vmin;
}

h2 {
  font-family:  'Bebas Neue', sans-serif;
  font-size:  10vmin;
  font-weight: lighter;
}

.Section {
  line-height: 1em;
  font-family:  'Bebas Neue', sans-serif;
  font-size:  10vmin;
  margin: 10vmin;
}

span:hover {
  background-color: yellow;
}


a {
    text-decoration: none;
  }

.navTop {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: black;
  color: white;
}

.navTop h1 {
  font-size: 8vmin;
}

.navLeft {
  line-height: 2.5em;
  margin: 0 2em;
}

.navLeft h1:hover {
  color: yellow;
}

.navRight {
  margin: 1em 2em;
  display: flex;
  flex-wrap: wrap;
}

.navLink {
  margin: 1em;
  line-height: 1em;
  color: white;
}

.navHome {
  color: white;
}

.navButton {
  background-color: transparent;
  border: none;
  font-family: 'Lato', sans-serif;
  font-size: 4vmin;
  /* stops "dev" button from wrapping oddly  */
  padding: .5em 1em;
}

.navButton a {
  color:white;
}

.navButton a:hover {
  color:black;
}

.navBottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  justify-content: flex-end;
  color: black;
  background-color:#ffffa1;
}

.navBottom a {
  color:black;
}

.navBottom a:hover {
    background-color: transparent;
    color:white;
}

.navBottomText {
  line-height: 1em;
  margin: 0.5em 2em;
}
.Footer {
    background-color: black;
    color: white;
    position: fixed;
    bottom: 0;
    padding: 0.5vmin;
    font-size: 2.5vmin;
    width: 100%;
}

.footerLink a {
  color:yellow;
}

.footerLink a:hover {
  color:black;
}
.AllVideos {
    margin: 3vmin 0 20vmin 0;
}

.VideoList{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
}

.GigWrapper {
    padding: 2vmin 0;
    height: 25vmin;
    width: 70vmin;
    display: flex;
}

.VideoImg{
    height: 100%;
}

.VideoImg img {
    border-radius: 50px;
    height: 100%;
}

.VideoImg img:hover{
    opacity: 0.5;
}

.VideoImg a {
  background-color: transparent;
}

.TextBox {
    padding: 0 0 0 3vmin;
    height: 100%;
    text-align: left;
    justify-content: center;
    display: flex;
    flex-direction: column;
}

.TextBox a {
    color: black;
}

.TextBox h1 {
    font-size: 6vmin;
    text-align: left;
    margin: 1vmin 0 0.5vmin 0;
}

.TextBox p {
    font-size: 2.5vmin;
    text-align: left;
    margin: 0;
    padding: 0.5vmin 0 1vmin 0;
}
